.keypad {
    height: auto;
    width: 328px;
    margin-top: 7px;
}

.keypad-row {
    display: flex;
    justify-content: center;
    padding: 0;
    margin: 0;
}

.keypad-row > button {
    font-size: 27px;
    overflow: hidden;
    height: 43px;
    margin: 4px;
    line-height: 0;
    font-family: monospace;;
    font-weight: lighter !important;
}

.keypad-primary-btn {
    background-color: #FBFBFB;
    color: #20232A;
    font-weight: bold;
}
.off-btn{
    margin-right: -12px !important;
}

.keypad-secondary-btn {
    background-color: #585555;
    color: #fff;
    border-top-left-radius: 7px;
    border-top-right-radius: 7px;
    border-bottom-left-radius: 18px;
    border-bottom-right-radius: 18px;
    width: 48px;
}
.first-row
{
    height: 62px;
    display: table;
    width: 100%;
}
.small-button
{
        height: 30px !important;
        font-size: 20px !important;
        padding: 0 !important;
}


.keypad{
    padding: 17px;
}
.keypad-model
{
    width: 200px;
    float: left;
    left: 56px;
    position: relative;
    height: 50px;
    margin: 0;
    top: 10px;
}
.first-row-btn{
    width: 288px;
    float: right;
    position: absolute;
    right: 0;
}
.last-row{
    width: 238px;
    margin-top: -52px;
    margin-right: 28px;
} 
.plus-btn{
    height: 97px !important;
}  
.turnOn
{
    font-size: 9px;
    margin-left: 27px;
    margin-top: -4px;
}